.case {
  background-color: #eaeaea;
}

.case__wrapper {
  margin: 0;
  padding: 0 20px;
}

.case__title {
  font-family: "OswaldRegular", sans-serif;
  font-weight: 400;
  color: #000000;
  font-size: 36px;
  line-height: 40px;
  padding-left: 20px;
}

.case__description {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $darkgrey;
}

.case__result {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.case__number {
  font-family: "OswaldRegular", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  padding: 5px 15px;
  border: 1px solid #cdcdcd;
  margin-right: 30px;
}

.case__text {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #c4c4c4;
}

.case__cost {
  font-family: "OswaldRegular", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
}

.case__cats-image {
  position: relative;
  width: 280px;
  height: 232px;
}

.case__cat-fat::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("..//..//img//index-background//index-cat-fat.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 280px 232px;
}

@media (min-width: $tablet-width) {
  .case {
    background-image: linear-gradient(to bottom, #ffffff 70%, #eaeaea 30%);
  }

  .case__wrapper {
    padding: 0 30px;
  }

  .case__title {
    font-size: 60px;
    line-height: 60px;
    text-align: start;
    padding: 0;
    margin: 0;
  }

  .case__description {
    font-size: 16px;
    line-height: 24px;
    padding: 51px 0 0 0;
  }

  .case__results {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .case__result {
    margin: 0;
    padding: 0;
  }

  .case__number {
    font-size: 30px;
    line-height: 37px;
    text-transform: uppercase;
    margin-right: 42px;
    padding: 15px 20px;
  }

  .case__text {
    font-size: 14px;
    line-height: 14px;
  }

  .case__cost {
    font-size: 20px;
    line-height: 20px;
    padding: 0 0 0;
  }

  .case__cats-image {
    padding: 0 80px;
    margin: 0;
  }

  .case__range {
    padding: 0 80px;
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .case {
    background-image: linear-gradient(to bottom, #ffffff 20%, #eaeaea 20%);
  }

  .case__wrapper {
    padding: 0 110px;
  }

  .case__information {
    display: flex;
    justify-content: space-between;
  }

  .case__results {
    display: flex;
    flex-direction: column;
  }

  .case__result {
    margin: 69px 0 0 0;
    padding: 0;
  }

  .case__title {
    font-size: 36px;
    line-height: 40px;
  }

  .case__description {
    font-size: 16px;
    line-height: 24px;
    padding: 0 784px 0 0;
    margin: 70px 0 0 0;
  }
}
